import moment from 'moment-timezone'

const Pet = ({ action, onClick, isDone }) => {
  const userTimezone = Intl.DateTimeFormat().resolvedOptions().timeZone
  // Convert the server time to the user's local time
  const localTime = moment
    .tz(isDone?.time, 'UTC')
    .tz(userTimezone)
    .format('HH:mm')
  return (
    <div>
      <div className="pet-items">
        {isDone?.done === true ? (
          <>
            <p className="done-task">{action} </p>
            <span className="check-span"></span>
          </>
        ) : (
          <p>{action}</p>
        )}

        {isDone?.done === true ? (
          <div className="button-div">
            <p className="done-pet-item">{localTime}</p>
          </div>
        ) : (
          <div className="button-div">
            <button onClick={() => onClick(action)}></button>
          </div>
        )}
      </div>
    </div>
  )
}

export default Pet
